/* roboto-300 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../../fonts/roboto-v20-latin-300.woff2") format("woff2"),
    url("../../fonts/roboto-v20-latin-300.woff") format("woff");
}
